import stylez from "../components/Previewer.module.css"
import { useState } from "react"
import marked from "marked"
import deftext from "./defaultText"

marked.setOptions({
    breaks:true,
})
const renderer = new marked.Renderer();

function Previewer (){
    const [text, setText] = useState(deftext);

    return( <div><textarea id="editor" style={stylez.textarea} type="text" rows="11" value={text} onChange = {(e) => setText(e.target.value)}></textarea>
    <h2>Output</h2>
    <Output markdown = {text}/>
    </div>)
}
function Output ( {markdown} ){
    return (<div className={stylez.box}
        id="preview"
        dangerouslySetInnerHTML = {{__html: marked(markdown, {renderer:renderer}),
    }}
    ></div>)
}
export default Previewer